:root{
    --square-side-length: 40px;
    --crossword-size:5
}
.crossword {
    font-family: "Helvetica"
}
.actions-menu{
    display:grid;
    grid-template-columns:repeat(3,1fr);
    gap: 10px;
}
.board-and-clues {
    gap:10px;
    display:grid;
    grid-template-columns: repeat(3,1fr);
}
.square {
    width: var(--square-side-length);
    height: var(--square-side-length);
    border: 1px solid #282828;
    color: black;
    text-align: center;
    font-size: x-large;
    z-index: 1
}
.square-black {
    background-color: black;
}
.square-wrong {
    color:red;
}
.square-correct{
    color:#0000ff
}
.square-user {
    background-color:#ffcc00;
    opacity: .9;
}
.square-in-row {
    background-color: #8BCCF7;
}
.board {
  display: grid;
  gap: 0px 0px;
}
.clue-highlighted {
    background-color: #8BCCF7
}
.clue-number {
    font-size: large;
    font-weight: bold;
}
.grid-clue-number {
    font-size: small;
    color: black;
    position: relative;
    right: 5px;
    z-index: 2;
}
.action-button {
    border: 1px solid #000000;
    text-align:center;
    vertical-align: middle;
    border-radius: 7px;
    background-color: #FFFFFF;
    transition: 0.3s;
    cursor:pointer;
    height: 40px;
    text-transform:uppercase;
    font-size: medium
}
.action-button:hover {
    background-color: #8BCCF7;
    color: black;
}
.clue {
    cursor:pointer;
}
